"use client";
import { Header } from "@/components/navigation/header";
import { Sidebar } from "@/components/navigation/sidebar";
import { TagBar } from "@/components/navigation/tag-bar";
import { ENDPOINTS } from "@/shared/site";
import { getRequest } from "@/utils/axios.client";
// import { cookies, headers } from "next/headers";
import { useEffect } from "react";

export async function getData() {
  const cookieStore = await cookies();
  const access_token = cookieStore.get("access_token");
  const refresh_token = cookieStore.get("refresh_token");

  const headersList = await headers();
  const userAgent = headersList.get("user-agent");

  const response = await fetch(
    `${ENDPOINTS.http_server_base}${ENDPOINTS.transaction_list}`,
    {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "User-Agent": userAgent || "",
        Cookie: `access_token=${access_token?.value || ""}; refresh_token=${
          refresh_token?.value || ""
        }`,
      },
      cache: "no-store",
    }
  );

  if (!response.ok) {
    throw new Error(`Failed to fetch: ${response.status}`);
  }

  const data = await response.json();
  return data;
}

export default function User() {
  // const data = await getData();
  const get_data = async () => {
    try {
      const { data } = await getRequest({ url: ENDPOINTS.transaction_list });
    } catch {}
  };
  useEffect(() => {
    get_data();
  }, []);
  return (
    <main>
      <TagBar />
      <div className="flex">
        <Sidebar />
        <div className="flex-1 min-h-screen pt-6">
          <Header />
          <div className="h-screen p-4">{/* <CustomTable /> */}</div>
        </div>
      </div>
    </main>
  );
}
